// components/ContactForm.tsx
import React from "react";

const ContactForm = () => {
  return (
    <div className="min-h-screen flex flex-col items-center justify-start px-4 py-12 bg-white text-gray-800">
      <h1 className="text-3xl font-bold mb-2">Contact Us</h1>
      <p className="text-center mb-8 text-gray-600">
        Fill out the form below or email us directly at{" "}
        <a href="mailto:oigarak@gmail.com" className="text-blue-600 underline">
          oigarak@gmail.com
        </a>
      </p>

      <form className="w-full max-w-xl space-y-4">
        <input
          type="text"
          placeholder="Name"
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        />
        <input
          type="email"
          placeholder="Email"
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        />
        <input
          type="text"
          placeholder="Subject"
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        />
        <input
          type="tel"
          placeholder="Phone Number"
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        />
        <select className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400">
          <option>Select Service Type</option>
          <option>App Development</option>
          <option>Electronics Design</option>
          <option>Computer Servicing</option>
          <option>IoT Solutions</option>
        </select>
        <textarea
          placeholder="Your Message"
          rows={5}
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400"
        ></textarea>

        <button
          type="submit"
          className="w-full bg-blue-600 text-white font-semibold py-2 rounded-lg hover:bg-blue-700 transition duration-200"
        >
          Send Message
        </button>
      </form>
    </div>
  );
};

export default ContactForm;
